
*{
	margin: 0;padding: 0;
	text-decoration: none;
	list-style-type: none;
	font-style:normal ;
}
.header{
	width: 100%;display: -webkit-box;display: -ms-flexbox;display: flex;-webkit-justify-content: space-between;
	background-color:#D44040 ;height: 2.25rem;line-height: 2.25rem;
	padding: 0 .75rem;
}
.navTop{
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;-webkit-justify-content: space-between;width: 100%;
	padding: 0 .75rem;font-size: .8rem;background: #F4F5F6;
}
.navTop li{
	width: 20%;text-align: center;line-height: 1.85rem;
}
.navTop li a{
	color: #000000;
}



